import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { FcyDealNumberRoutingModule, fcydealroutes } from './fcy-deal-number-routing.module';
import { RouterModule } from '@angular/router';
import { FcyDealNumberComponent } from './fcy-deal-number.component';
import { AngularMaterialModule } from 'src/app/shared/angular-material/angular-material.module';
import { FormsModule } from '@angular/forms';
import { BatchDeletionComponent } from './batch-deletion/batch-deletion.component';

@NgModule({
  declarations: [FcyDealNumberComponent, BatchDeletionComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(fcydealroutes),
    AngularMaterialModule,
    FormsModule
  ]
})
export class FcyDealNumberModule { }
