import { Component, OnInit, ViewChild, ViewChildren, QueryList, ChangeDetectorRef } from '@angular/core';
import { MatPaginator, MatTableDataSource } from '@angular/material';
import { FcyDealNumberService } from './fcy-deal-number.service';
import { FcyDealNumber, medUploadPk } from './FcyDealNumber';
import Swal from 'sweetalert2';

/**Fayaz added */
import { RoleService } from '../roles1/roles.service';
import { permissionsLabels } from '../roles1/models/fmosNewRolePermissions';
//

@Component({
  selector: 'app-fcy-deal-number',
  templateUrl: './fcy-deal-number.component.html',
  styleUrls: ['./fcy-deal-number.component.css']
})
export class FcyDealNumberComponent implements OnInit {
  // displayedColumns: string[] = ['batchNo', 'account', 'amount','accountJournal','accountBranch','save'];
  displayedColumns: string[] = ['batchNo', 'branchCode', 'currNo', 'initiationDate', 'sourceCode', 'account', 'amount', 'acumenJournal'];

  @ViewChildren(MatPaginator) paginator = new QueryList<MatPaginator>();

  selectedBatchNo: Object;
  batchNumber: any;
  dataSource = new MatTableDataSource();
  batchTableResp: any;
  showTable: boolean = false;

  medUploadPk2: medUploadPk;

  //added by fayaz

  roleCodes = new permissionsLabels();
  disbaleSave: boolean;
  //

  constructor(private fcyService: FcyDealNumberService,
     private roleService: RoleService,private ref:ChangeDetectorRef) { }
  fcyDealNumber: any;

  ngOnInit() {
    this.fetchingBatchNo();
    console.log("vidya", this.fcyDealNumber);

    //added by fayaz
    setTimeout(() => {
      this.newRolePermissions();
    }, 2000);
    this.roleService.screenLabelList.subscribe(message => this.roleCodes = message);


  }
  //dropdown to fetch batch no
  fetchingBatchNo() {
    console.log("this is to fetch batch number");
    this.fcyService.fetchingBatchNo().subscribe(batchNumberResp => {
      console.log(batchNumberResp)
      this.selectedBatchNo = batchNumberResp
    })

  }//checking batch no is coming or not and //sending batch no to backend
  // onSelectionBatchNo(batch) {
  //   console.log(batch);
  //   this.batchNumber = batch;
  //   this.fcyService.sendingBatchNumber(this.batchNumber).subscribe(batchResp => {
  //     console.log("Table Resp", batchResp);
  //     this.fcyDealNumber = batchResp;
  //     console.log(this.fcyDealNumber);
  //     this.showTable = true;

  //     // console.log(this.medUploadPk);
  //     // this.dataSource = new MatTableDataSource<FcyDealNumber>(this.medUploadPk);
  //     this.dataSource.paginator = this.paginator.toArray()[0];;
  //     console.log(this.batchTableResp);
  //   })
  // }
  onSelectionBatchNo(batchNo) {
    this.ref.markForCheck();
    console.log(batchNo);
    // this.batchNumber = batch;
    this.ref.markForCheck();
    this.fcyService.sendingBatchNumber(batchNo).subscribe(batchResp => {
    console.log("Table Resp", batchResp);
    // this.fcyDealNumber = batchResp;
    this.ref.markForCheck();
    if(batchResp=='')
    {
      Swal.fire(
        {
          title: "No data for Batch:" + batchNo
        }
      );
      this.showTable=false;
    }
    else{
       this.fcyDealNumber = batchResp;
       this.showTable=true;
    }
    
    
   

    console.log(this.fcyDealNumber);
    
   
  
   
    // console.log(this.medUploadPk);
    // this.dataSource = new MatTableDataSource<FcyDealNumber>(this.medUploadPk);
    // this.dataSource.paginator = this.paginator.toArray()[0];;
    // console.log(this.batchTableResp);
    })
    }
  onSaveOfFcyDealNumber(data) {
    console.log("onSaveOfFcyDealNumber", data);
    this.fcyService.onSaveOfFcyDealNumber(data).subscribe(saveResp => {

      console.log(saveResp);
      if (saveResp) {
        Swal.fire({
          title: "Record Saved SuccessFully"

        });
        this.disbaleSave=true;
      }
      else {
        Swal.fire({
          title: "Failed Save the Record"
        })
      }
    })
  }

  //added by fayaz
  newRolePermissions() {
    this.roleService.fetchScreenPermissions('Treasury Reference');
  }
}
